/* Pages  ----------------------------  */


.contactBox {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 960px;
  max-width: 100%;
  .formTitle {
    text-align: center;
    text-transform: uppercase;
    /*+font(false, $fS4, false, false, false) */
    &::before,
    &::after {
      content: " — "; } }
  .form {
    width: 90%;
    margin: 0 auto;
    .formGroup {
      margin-top: 3rem;
      text-align: center; }
    input, textarea {
      width: 100%;
      padding: 1rem;
      background-color: rgba($white, 0.4);
      /*+font($ssFF, false, false, 600, false) */
      transition: all .35s ease-in-out;
      &:focus {
        background-color: rgba(191, 127, 127, 0.6); } }
    .form_btn {
      width: inherit;
      /*+font($ssFF, false, $siteTextW, 600, false) */
      text-transform: uppercase;
      outline: none;
      border: none;
      background-color: rgba($white, 0.4);
      padding: 1rem 3rem;
      border-radius: 3rem;
      cursor: pointer;
      transition: all .3s ease-in-out;
      &:hover {
        /*background-color: rgba($highlight, 0.2) */ }
      &:active {
        transform: translateY(2px); } } } }



.termsTitle, .thanksTitle {
  @include font($toLoadSFF, 2em, $siteHdgsColor, 600, 2.5);
  background-color: $white;
  width: 100%;
  max-width: 100%;
  margin: 0 auto 1.5rem;
  min-height: 5rem;
  border-radius: 10px;
  text-align: center; }

.termsArticles, .thanksArticles {
  width: 100%;
  background-color: $white;
  border-radius: 10px;
  margin-top: 3em;

  .termsContent, .thanksContent {
    width: 90%;
    margin: 0 auto;
    padding: 2em 0;
    p {
      margin-top: 0;
      @include font(monospace, 1.3em, $siteTextColor, 400, 1.4); }
    ol, ul {
      margin: 1em 0;
      list-style: none;
      li {
        @include font(monospace, 1.3em, $siteTextColor, 400, 1.4);
        list-style-type: disc; } } } }
