/* Links  ----------------------------  */

a {
  @include font($toLoadSSFF, false, false, 500, false);
  text-decoration: none;
  &:focus,
  &:active,
  &:visited {
    color: #4000f3;
    text-decoration: none; }
  &:hover, {
    color: $red; } }

.readMore {
  @include font(false,0.8em,$red,400,false);
  white-space: nowrap;
  background-color: transparent;
  border: 1px solid transparent;
  padding: 8px 16px 8px 8px;
  border-radius: 5px;
  &:focus,
  &:active,
  &:visited {
    @include font(false,false,$red,false,false); }
  &:hover {
    @include font(false,false,$bludark,false,false);
    border: 1px solid $red;
    background-color: $greybtn; } }
