/* Layout  ---------------------------  */

.l-siteWrapper {
  @include position(relative, null, null, null, null);
  margin: 0;
  max-width: calcEM($sitewh); }

.l-sidebar {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  @include position(fixed, 0, null, 0, 0);
  width: 240px;
  height: 100%;
  padding: 20px 10px;
  background-color: $white; }

.l-content-box {
  padding: 20px;
  padding-left: 260px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: stretch; }

.l-post-article {
  @include position(relative, null, null, null, null);
  border-radius: 20px;
  background-color: $white;
  padding: 0.5em; }

