/* Buttons  --------------------------  */


#newsLetterBtn {
  width: 170px;
  padding: 1.35rem;
  @include font($toLoadSSFF, 1.2em, false, 700, 1.6);
  text-align: center;
  text-transform: capitalize;
  cursor: pointer;
  border-radius: 2.5rem;
  border: none;
  outline: none;
  @include position(absolute,-77px,0,null,null);
  @include btntogrey($white,$red); }

#contactBtn {
  &[type="submit"] {
    text-transform: uppercase;
    cursor: pointer; }
  @include font(false, 1.15em, false,700,false);
  @include btntogrey($white,$red); }

.redBtn {
  display: inline-block;
  a, span {
    border-radius: 5px;
    padding: 8px 16px;
    @include btntogrey($white,$red); }
  a:hover:not(.active) {
    @include font(false, false, $bludark, 600, false);
    background-color: $greybtn; } }

.redBtn.btnReadMe {
  opacity: 1;
  @include font(false,0.625em,false,false,false);
  @include position(absolute, null, 15px, 15px,null);
  margin: 0;
  padding: 0;
  border-radius: 8px;
  &:before, &:after {
    @include clearfix; }
  &:hover {
    opacity: 0.9; } }

.btnCockies {
  @include btntogrey($white,$red);
  border-radius: 5px;
  padding: 4px 16px; }
