/* Media  ----------------------------  */
img,
video,
object {
  max-width: 100%;
  height: auto;
  border: none;
  outline: none; }

figure {
  max-width: 100%; }

figcaption {
  margin: 0 0 0.5em 0;
  @include font(false, 1rem, $bludark, 400, 2); }

img.mapHere {
  display: inline;
  @include size(50px);
  margin-right: 8px;
  border-radius: 50%; }

.progressive {
  position: relative;
  display: block;
  overflow: hidden;
  outline: none;
  cursor: pointer;
  img {
    display: block;
    width: 100%;
    max-width: none;
    height: auto;
    border: 0 none;
    &.preview {
      filter: blur(2vw);
      transform: scale(1.05); }
    &.reveal {
      position: absolute;
      left: 0;
      top: 0;
      will-change: transform, opacity;
      animation: progressiveReveal 1s ease-out; } } }

@keyframes progressiveReveal {
  0% {
    transform: scale(1.05);
    opacity: 0; }

  100% {
    transform: scale(1);
    opacity: 1; } }
