/* Sections  -------------------------  */

section {
  width: 100%; }

.sectionPagination,
.sectionNewsLetter,
.sectionTags,
.sectionContattami,
.sectionTerms,
.sectionThanks {
  margin: 2em 0; }

.sectionContact {
  height: 90vh;
  min-height: 500px;
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: auto; }

.sectionWhere {
  margin-bottom: 2em;
  .whereWrapper {
    margin: 0 auto;
    .map {
      margin: vr(1) auto;
      height: 450px;
      width: 100%; } } }

.sectionPhotogallery {
  text-align: center;
  margin: 0 auto;
  @include clearfix;
  .responsive {
    width: 50%;
    padding: 0 6px;
    float: left;

    figure.gallery {
      border: 1px solid $grey050; }

    figure.gallery:hover {
      border: 1px solid $grey900; } } }
