.gallery {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;

  img {
    width: 100%;
    border-radius: 4px;
    transition: all 0.2s ease-in-out; }

  > a {
    display: block; }

  &.half {
    > a,
    > img {
      width: calc(50% - 0.5em); }
    > span {
      display: block;
      width: calc(50% - 0.5em); }

    figcaption {
      width: 100%; } }

  &.third {
    > a,
    > img {
      width: calc(33.3333% - 0.5em); }
    > span {
      display: block;
      width: calc(33.3333% - 0.5em); }

    figcaption {
      width: 100%; } }

  &.fourth {
    > a,
    > img {
      width: calc(25% - 0.4em); }
    > span {
      display: block;
      width: calc(25% - 0.4em); }

    figcaption {
      width: 100%; } } }

@media only screen and (max-width : $tablet) {
  .gallery {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    &.half,
    &.third,
    &.fourth {
      > a,
      > img {
        width: calc(100% - 0.5em); }
      > span {
        width: calc(100% - 0.5em); } } } }
