/* Tooltips from Refills  ------------  */
#notaContactBtn-item {
  $base-border-color: gainsboro !default;
  $base-border-radius: 3px !default;
  $base-line-height: 1.5em !default;
  $dark-gray: $grey900 !default;
  $base-font-color: $bludark !default;
  $tooltip-background: $greybtn;
  $tooltip-color: $bludark;
  $tooltip-max-width: 30em;
  $tooltip-arrow-width: 8px;
  $tooltip-shadow: 0 2px 2px silver;
  $tooltip-distance-from-item: 4em;
  $tooltip-arrow-distance-from-box: -1.4em;

  //background: $tooltip-background
  //border: 1px solid $base-border-color
  //border-radius: $base-border-radius
  display: inline-block;
  padding: 0.5em 1em;
  position: relative;
  text-align: center;

  &:focus,
  &:hover .notaContactBtn {
    opacity: 1;
    visibility: visible; }


  .notaContactBtn {
    //+position(absolute, null, 0, $tooltip-distance-from-item, 0)
    background-color: $tooltip-background;
    border-radius: $base-border-radius;
    box-shadow: $tooltip-shadow;
    border: 1px solid red;
    /* Make sure you use -webkit-backface-visibility: hidden on the body element to prevent 1px nudging bugs. */
    @include font(monospace, 0.9em, $tooltip-color, false, 1.5);
    margin: 0 auto;
    max-width: $tooltip-max-width;
    opacity: 0;
    padding: 1em;
    text-align: center;
    transition: all 0.2s ease-in-out;
    visibility: hidden;
    z-index: 10; }

  .notaContactBtn.formBtn {
    @include position(absolute, null, 0, $tooltip-distance-from-item, 0);
    p {
      /*+font(false, 1em, $bludark, 600, 0) */
      margin: 0; }
    &:after {
      @include position(absolute, null, 0);
      border: $tooltip-arrow-width solid transparent;
      bottom: $tooltip-arrow-distance-from-box;
      @include font(false, 1.4em, $red, false, false);
      content: "▼";
      margin-left: -$tooltip-arrow-width;
      text-align: center;
      text-shadow: $tooltip-shadow; } }

  .notaContactBtn.newsBtn {
    @include position(absolute, -60px, null, null, 20%);
    p {
      /*+font(false, 1em, $bludark, 600, 0) */
      margin: 0; }
    &:after {
      @include position(absolute, null, 0);
      border: $tooltip-arrow-width solid transparent;
      bottom: $tooltip-arrow-distance-from-box;
      @include font(false, 1.4em, $red, false, false);
      margin-left: -$tooltip-arrow-width;
      text-align: center;
      text-shadow: $tooltip-shadow; } } }
