/* Mixin  ----------------------------  */

@mixin clearfix {
  &:before {
    content: "";
    display: table; }
  &:after {
    content: "";
    display: table;
    clear: both; } }

@mixin font($ff: false, $size: false, $colour: false, $weight: false, $lh: false) {
  @if $ff {
    font-family: $ff; }
  @if $size {
    font-size: $size; }
  @if $colour {
    color: $colour; }
  @if $weight {
    font-weight: $weight; }
  @if $lh {
    line-height: $lh; } }

@mixin mediaQuery($width) {
  @media screen and (min-width: $width) {
    @content; } }

@mixin position($position, $top: null, $right: null, $bottom: null, $left: null) {
  position: $position;
  top: $top;
  right: $right;
  bottom: $bottom;
  left: $left; }

@mixin size($b: null, $a: null) {
  @if $a {
    width: $b;
    height: $a; }
  @else {
    width: $b;
    height: $b; } }

@mixin textJ {
  text-align: justify;
  word-wrap: normal;
  word-break: normal;
  hyphens: auto; }

@mixin cv {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

@mixin btntogrey ($fc: $siteTextColor, $bc: $siteHdgsColor) {
  @include font(false, false, $fc, false, false);
  background-color: $bc;
  border: 1px solid $bc;
  transition: background-color .35s ease-in-out;
  &:visited {
    @include font(false, false, $white, false, false); }
  &:hover {
    @include font(false, false, $bludark, false, false);
    background-color: $greybtn;
    border: 1px solid $red; } }
