/* Text  -----------------------------  */

p {
  @include font($toLoadSSFF,false,$bludark,false,false);
  position: relative;
  font-style: normal;
  letter-spacing: normal;
  text-transform: none; }

p + p {
  /* margin-bottom: 0 0 vr(1) 0 */ }

.highlighter-rouge {
  @include font(false,false,$bludark,false,false);
  border: 1px solid $text;
  border-radius: 3px;
  padding: 0.1rem 0.5rem; }

blockquote {
  border-left: 5px solid $grey500;
  color: $grey600;
  margin: 0 1em;
  padding: 0 0 0 1em; }

blockquote.aggiornamento {
  border-left: 5px solid $red; }

.rosso {
  border-left: 5px solid $red; }

.quando, .itinerario {
  @include font($toLoadSSFF, 1rem, $grey700, 400, false);
  margin-top: -25px;
  font-variant: small-caps; }
.itinerario {
  margin-top: -20px; }
