/* Print  ----------------------------  */
@media print {
  @page {
    size: A4 prtrait; }
  body {
    background-color: $white; }

  .l-sidebar {
    @include position(relative, null, null, null, null);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    padding: 10px;
    background-color: #ffffff;
    box-shadow: 0 1px 1px 0 rgba(31, 35, 46, .15);
    footer {
      margin-bottom: 10px; } }
  .o-about {
    //text-align: center
    max-width: 480px;
    margin: 10px auto auto;
    img {
      @include size(100px);
      border-radius: 100%;
      margin-bottom: 10px; } }
  .l-content-box {
    padding: 20px 10px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: stretch; }
  .contact-title, .copyright, .breack {
    display: none; }
  .post-title {
    @include font($toLoadSFF, 12px, $siteHdgsColor, 400, 1.1); }

  .doNotPrint {
    display: none !important; }
  .pageBreakAfter {
    page-break-after: always; }
  .doNotPageBreakInside {
    page-break-inside: avoid; } }
