/* Query  ----------------------------  */

@media only screen and (max-width : $screen) {
  .o-post-index {
    width: 48%;
    max-width: 100%;
    margin: 0 0.9% 1.5%;
    flex-direction: column;
    .post-thumbnail {
      width: 100%; }
    .post-content {
      width: 100%;
      .post-title {
        margin: 0 0 5px;
        a {
          @include font(false, 21px, false, false, false); } } } } }
.on {
  display: inline-block; }
.off {
  display: none; }

.l-siteFooter {
  .o-copyright {
    display: none; } }

/* Small Devices, Tablets */
@media only screen and (max-width : $tablet) {
  .l-sidebar {
    @include position(relative, null, null, null, null);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    padding: 10px;
    background-color: #ffffff;
    box-shadow: 0 1px 1px 0 rgba(31, 35, 46, .15);
    .o-copyright, .breack {
      display: none; }
    footer {
      margin-bottom: 10px; } }
  .o-about {
    //text-align: center
    max-width: 480px;
    margin: 10px auto auto;
    img {
      @include size(100px);
      border-radius: 100%;
      margin-bottom: 10px; } }
  .l-content-box {
    padding: 20px 10px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: stretch; }
  .o-separator {
    .socialLink {
      margin: 0; } }
  .l-siteFooter {
    .o-copyright {
      display: block; }
    .breack {
      display: none; } }
  form#frmNewsLetter {
    #emailInput {
      width: 100%;
      padding: 0.46rem;
      @include font(false, 0.9em, false, false, 1); }
    #newsLetterBtn {
      width: 100px;
      padding: 0.5rem;
      @include font(false, 0.9em, false, false, 1);
      @include position(absolute,-35px,0,null,null); } }
  .on {
    display: none; }
  .off {
    display: inline-block; }
  .nofloat {
    float: none;
    max-width: 100%;
    text-align: center;
    margin-left: auto;
    margin-right: auto; }
  .hide {
    display: none; } }


/* Extra Small Devices, Phones */
@media only screen and (max-width : $phones) {
  .l-content-box {
    padding: 10px; }
  .o-post-index {
    width: 98%;
    max-width: 100%;
    margin: 2%;
    flex-direction: column;
    .post-thumbnail {
      width: 100%; }
    .post-content {
      width: 100%;
      .post-title {
        margin: 0 0 5px;
        a {
          @include font(false, 21px, false, false, false); } } } }
  .sectionContact {
    height: 100%; }
  #notaContactBtn-item {
    .notaContactBtn.newsBtn {
      @include position(absolute, null, 0, null, 0); } } }
