/* Forms  ----------------------------  */

form, fieldset, label, input, textarea {
  @include font(monospace, false, false, false, 1);
  box-sizing: border-box;
  width: 100%; }

form {
  padding: 0 calcEM(20px);
  margin: 0 auto; }

fieldset {
  border: none;
  vertical-align: middle; }

label {
  display: block;
  @include font(false, 1.163em, $bludark, 600, false);
  padding-left: 5px;
  padding-bottom: 5px; }

input, textarea {
  border: 2px solid $red;
  padding: 9px;
  transition: all .35s ease-in-out;
  @include font(false, 1.1em, $bludark, 500, false);
  &:focus {
    outline: none;
    background-color: rgba($red, 0.1); } }

textarea {
  resize  : none;
  overflow: auto;
  height: 10rem; }

