form#frmContact {
  .formNota {
    margin: 1em 0;
    span {
      display: block;
      @include font($toLoadSSFF, 0.75em,$bludark,false,1.3);
      text-indent: 2em;
      margin-top: -1.3em;
      text-align: justify; } } }

form#frmNewsLetter {
  width: 700px;
  max-width: 90%;
  margin: 0 auto;
  label {
    display: block;
    margin: 2em auto -18px;
    /*font-variant: small-caps */ }
  .newsLetterBox {
    padding: 2rem 0;
    position: relative; }
  #emailInput {
    width: 100%;
    padding: 1.35rem 3rem;
    border-radius: 2.5rem;
    @include font($toLoadSFF, 1.2em, $siteTextColor, 700, 1.6);
    /*text-transform: capitalize */
    border: none;
    outline: none; }
