/* Post  -----------------------------  */

.o-post-cover-image {
  @include position(relative, null, null, null, null); }

.o-post-tag {
  /*float: right */
  text-align: right;
  @include font(false, 1em, $siteTextColor, 400, 1);
  margin: 0 2em 2em 0;
  .post-tag-title {
    @include font(false, false, false, 700, 1.5);
    font-variant: small-caps;
    margin: 0 8px 0 0; }
  .post-tag {
    //display: inline-block
    background-color: $bludark;
    padding: 0.2rem .5rem;
    @include font(false, 0.875em, $greyblu, 500, false);
    margin-right: .5rem;
    border-radius: 4px;
    text-decoration: none;
    transition: all .1s ease-in-out;
    &:before {
      content: "\f02b";
      font-family: "Font Awesome 5 Free";
      font-weight: 600;
      padding-right: .5em; }
    &:hover {
      @include font(false, false, $bludark, false, false);
      text-decoration: none;
      background-color: $greyblu; } } }

.o-post-wrapper {
  margin: 3em 0 0 0;
  .post-header {
    .post-title {
      @include font($toLoadSFF, 2em, $siteHdgsColor, false, 1); }
    .post-date {
      @include font(false, 0.75em, $siteTextColor, 400, 1);
      margin-top: -20px; }
    .post-update {
      @include font(false, 0.75em, $red, 400, 1);
      margin-top: 5px; } }
  .post-content-wrapper {
    @include font(false, 1.125em, $siteTextColor, 400, 1.4);
    text-align: justify; }
  .post-footer {
    @include position(relative, null, null, null, null); } }
