/* Objects ----------------------------  */

.o-about {
  margin: 0 0 40px;
  .cover-author-image {
    @include size(100px);
    margin: 0 auto 10px;
    border-radius: 100%;
    overflow: hidden;
    background-color: $grey900; }
  .author-image {
    @include size(100px);
    border-radius: 50%;
    transition: transform 0.35s;
    &:hover {
      transform: scale3d(0.90,0.90,1); } }
  .author-name {
    position: relative;
    @include font($toLoadSFF, 1em, $siteTextColor, 600, false);
    text-transform: capitalize; }
  .author-job {
    text-transform: uppercase;
    font-variant: small-caps;
    color: $red; }
  .tori {
    @include size(32px);
    margin: 15px 0; }

  p {
    @include font(false, 0.8em, $siteTextColor, 400, false);
    margin: 0 0 10px; } }

.o-separator {
  .separator-title {
    position: relative;
    @include font($toLoadSFF, 0.75em, $siteTextColor, 400, false);
    text-transform: uppercase;
    margin: 0 0 5px;
    &::before {
      content: "";
      display: block;
      height: 2px;
      width: calc(50% - 48px);
      transform: translateY(-50%);
      @include position(absolute, 50%, null, null, 0);
      background-color: $grey600; }
    &::after {
      content: "";
      display: block;
      height: 2px;
      width: calc(50% - 48px);
      transform: translateY(-50%);
      @include position(absolute, 50%, 0, null, null);
      background-color: $grey600; } }
  .short {
    &::before, &::after {
      width: calc(50% - 100px); } }
  .post-separator {
    margin: 0 5%;
    &::before {
      width: calc(50% - 70px); }
    &::after {
      width: calc(50% - 70px); } } }

.o-copyright {
  @include font(false, 0.875em, $siteTextColor, 400, false);
  margin: 0;
 }  //margin-bottom: 2em

.o-post-index {
  width: 100%;
  max-width: 100%;
  margin-bottom: 1.5rem;
  background-color: $white;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  min-height: 11rem;
  border-radius: 10px;
  overflow: hidden;
  transition: all .3s ease;
  box-shadow: 0 1px 1px 0 rgba(31, 35, 46, .15);
  &:hover {
    transform: translate(0px, -2px);
    box-shadow: 0 15px 45px -10px rgba(10, 16, 34, .2); }

  .post-thumbnail {
    width: 30%;
    max-width: 100%;
    min-height: 11rem;
    background-size: cover;
    background-position: 50% 50%; }

  .post-index-content {
    padding: 1rem;
    width: 70%;
    .post-title {
      margin: 0 0 10px;
      a {
        @include font($toLoadSFF, 1.875em, $siteHdgsColor, 600, false);
        text-decoration: none; } }
    p {
      margin-top: 0;
      @include font(false, 1em, false, false, false); }
    .post-date,
    .post-words {
      white-space: nowrap;
      @include font(false, 0.75em, false, false, false); }
    .post-words {
      margin-left: 0.5rem; } } }

.o-griglia {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: center;
  .item {
    flex-grow: 1;
    flex-basis: auto; } }

.o-relatedPosts {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  //margin: 0
  padding: 0;

  .c-relatedPost {
    display: flex;
    padding: 1rem;

    .post {
      position: relative;
      max-width: 420px;
      box-shadow: 0 20px 40px -14px rgba(0, 0, 0, 0.25);
      .cardImage {
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;
        -webkit-filter: contrast(70%);
        filter: contrast(70%);
        transition: filter 0.5s cubic-bezier(0.43, 0.41, 0.22, 0.91); }
      &:hover .cardImage {
        -webkit-filter: contrast(100%);
        filter: contrast(100%); } } }

  @media (min-width: 40rem) {
    .recentPost {
      width: 50%; } }
  @media (min-width: 56rem) {
    .recentPost {
      width: 33.3333%; } } }

#o-cookies {
  padding: 15px 15px;
  display: none;
  text-align: center;
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: rgba(38, 57, 89, 0.8);
  color: rgba(255,255,255,1);
  a {
    cursor: pointer;
    margin-left: 10px; } }

@media (max-width: 767px) {
  #o-cookies {
    span {
      display: block;
      padding-top: 3px;
      margin-bottom: 13px; }
    a {
      position: relative;
      bottom: 4px; } } }
