/* Pagination ------------------------  */


.o-pagination {
  display: inline-block;

  a, span {
    float: left;
    @include font(false, false, $white, false, false);
    background-color: $bludark;
    text-decoration: none;
    transition: background-color .3s;
    border: 1px solid $greybtn;
    border-radius: 5px;
    padding: 8px 16px;
    margin: 0 4px; }
  a:hover:not(.active) {
    @include font(false, false, false, 600, false);
    background-color: $greybtn; }

  .page-index {
    background-color: $red;
    @include font(false, false, $white, 600, false);
    border: 1px solid $red; } }
