/* Components  -----------------------  */

.c-post-tag {
  padding: 0.2rem .5rem;
  border-radius: 4px;
  text-decoration: none;
  white-space: nowrap;
  transition: all .1s ease-in-out;
  @include btntogrey($white,$bludark);
  @include font(false, 0.75em, $greyblu, 500, 2);
  .tag {
    display: inline;
    margin: 8px 8px 0 0;
    @include size(12px);
    @include position(relative, 2px,null,null,null);
    fill: $white; }
  &:hover {
    .tag {
      fill: $bludark; } } }

.c-prenext {
  @include btntogrey($white,$bludark); }

.c-post-ora {
  padding: 0.2rem .5rem;
  margin-top: .5rem;
  border-radius: 4px;
  white-space: nowrap;
  @include font(false, 0.75em, $bludark, false, false);
  background-color: $greybtn;
  border: 1px solid $red; }

.c-h-titles {
  @include font(false, 2em, false, false, 3);
  text-align: center;
  text-transform: uppercase;
  background-color: $white;
  width: 100%;
  max-width: 100%;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 15px 45px -10px rgba(10, 16, 34, .2);
  transform: translate(0px, -2px); }

@media only screen and (max-width : $tablet) {
  .c-h-titles {
    @include font(false, 1.7em, false, false, false); } }
@media only screen and (max-width : $phones) {
  .c-h-titles {
    @include font(false, 1.2em, false, false, false); } }

.c-embedVideo {
  margin-bottom: 2em; }

.c-cardContent {
  flex: 1 1 auto;
  flex-direction: column;
  padding: 1rem;
  h5.cardTitle {
    letter-spacing: 2px;
    text-transform: uppercase; }
  .cardText {
    max-width: 400px;
    margin-bottom: 1.25rem; } }

.c-cardDate {
  background: linear-gradient(45deg, $bludark, $red);
  display: block;
  border-radius: 5px;
  width: 65px;
  padding: 8px;
  @include font(false, 0.875em,$white,600,1.5);
  opacity: 0.7;
  text-align: center;
  margin: -30px 0 0 30px;
  position: relative;
  z-index: 0;
  @include clearfix;
  date {
    display: block;
    letter-spacing: 2px; } }

.c-zz {
  border: 1px solid black;
  border-radius: 10px;
  //background-color:  rgba(255,0,37,0.02)
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center; }
