/* Footer  ---------------------------  */

.l-siteFooter {
  margin: 2em auto;
  p {
    @include font($toLoadSSFF, 9px, $grey700, 400, false);
    margin: 0; } }


.github {
  width: 15px;
  height: 15px;
  fill: #616161; }
.github:hover {
  fill: #FF0025; }
.privacy {
  position: static;
  top: 0;
  margin: 4em auto 1em;
  font-family: "Lato", sans-serif;
  font-size: 9px;
  color: transparent;
  font-weight: 600;
  line-height: 1;
  background-color: #FF0025;
  text-shadow: 5px 4px 3px rgba(255, 0, 37, 0.5);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
  letter-spacing: 1px; }
